@import 'styles/color.scss';

.groups {
  display: flex;
  flex: 1.5;
  padding-right: 0px;
  flex-direction: column;
  margin-right: 20px;

  .diagnosis-list {
    flex: 1;
    padding: 10px;

    &.has-error {
      border: 1px solid red;
      border-radius: 5px;
    }

    .title {
      margin-bottom: 10px;
    }

    .add-diagnosis-button {
      margin-top: 10px;
    }
  }

  span.error {
    padding-top: 1px;
    color: red;
  }
}
