@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: column;
  padding: 20px;
  margin-bottom: 20px;

  .table {
    margin-bottom: 20px;
    flex: 1;
  }

  .table-header {
    display: flex;
    padding-bottom: 10px;
    .table-description {
      flex: 1;
      color: $color-1;
    }

    .add-link {
      font-weight: 500;
      text-decoration: underline;
      cursor: pointer;
    }
  }

  .row {
    .align {
      &.center {
        text-align: center;
      }

      &.right {
        text-align: right;
      }
    }
  }
  .table-total {
    display: flex;
    padding-top: 10px;
    width: 100%;
    .caption {
      flex: 1;
      display: flex;
      justify-content: flex-end;
      padding-right: 20px;

      font-weight: bold;
    }

    .amount {
      font-weight: bold;
    }
  }
}
