.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    // display: flex;
    // flex-direction: row;

    .read-only-value {
      font-weight: unset;
      padding: 5px;
    }

    .text-field {
      flex: 1;
      display: flex;

      .input {
        width: 100%;
        padding: 10px;
        input {
          padding: 0 !important;
        }
      }
      // Errors
      &.has-error {
        border: 1px solid red;
        margin-bottom: unset;
      }
    }
    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
