.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .header {
    display: flex;
    padding-bottom: 5px;

    .delete {
      width: 35px;
      text-align: left;
      margin-right: 10px;
    }

    .description {
      flex: 1;
    }

    .code {
      width: 100px;
    }

    .primary {
      text-align: left;
      margin-right: 15px;
    }

    .discrepancy {
      width: 15px;
      text-align: left;
    }
  }

  .wrapper {
    padding-bottom: 20px;
    flex: 1;
    display: flex;
    align-items: center;

    &:last-child {
      padding-bottom: unset;
    }

    .delete {
      width: 35px;
      text-align: left;
      margin-right: 10px;
    }

    .description {
      flex: 1;
    }

    .code {
      width: 100px;
      margin-left: 10px;
    }

    .primary {
      text-align: left;
      margin-right: 15px;
    }
  }
}
