@import "styles/color.scss";

.root {
  padding: 20px;

  .groups {
    display: flex;
    flex-direction: row;
    padding-top: 10px;

    &.first-item {
      padding-top: unset !important;

      .input {
        padding-top: unset !important;
      }
    }

    .input {
      flex: 1;
      padding: 10px;

      &:first-child {
        padding-left: unset;
      }

      &:last-child {
        padding-right: unset;
      }
    }

    &.buttons {
      justify-content: flex-end;

      button {
        margin-left: 10px;
      }
    }
  }
}
