.root {
  padding-left: 70%;  
  position: absolute;  
  top: 20px;
  .input-wrapper {
    display: flex;
    flex-direction: row;    
    }

  &.disapproved {
    .input-wrapper {
      > :nth-child(1) {
        padding: 5px;
        background-color: hsl(34, 91%, 77%) !important;
        color: red;
      }
      .icon-container {
        font-size: 18px;
        height: 25px;
        width: 25px;
        margin-left: 5px;
        margin-top: 5px;
        margin-bottom: auto;
        padding: 3.4px;
        border-radius: 50%;
        background-color: hsl(34, 91%, 77%);
        color: red;
        // margin: auto;
        display: flex;
        cursor: pointer;
      }
    }
  }
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
