.root {
  display: flex;
  width: 100%;
  flex-direction: row;
  align-items: center;

  .input-wrapper {
    display: flex;
    width: 100%;
    flex-direction: row;
  }

  $read-only: 'no';

  &.discrepancy {
    div[class*='text-field'] {
      background-color: hsl(34, 91%, 77%) !important;
      margin-bottom: unset;
    }
    div[class*='autocomplete-field'] {
      background-color: hsl(34, 91%, 77%) !important;
    }
    div[class*='DropDown_input'] {
      background-color: hsl(34, 91%, 77%) !important;
    }
    div[class*='DropDown_input'] {
      background-color: hsl(34, 91%, 77%) !important;
    }
    h6[class*='read-only-value'] {
      background-color: hsl(34, 91%, 77%) !important;
      $read-only: 'yes';
    }

    .discrepancy-icon {
      height: 100%;

      @if $read-only == 'yes' {
        margin-bottom: 10px;
      } @else {
        margin-bottom: 50px;
      }

      align-self: flex-end;
      .icon {
        display: block;
        font-size: 18px;
        height: 25px;
        width: 25px;
        margin-left: 5px;
        padding: 3.4px;
        border-radius: 50%;
        background-color: hsl(34, 91%, 77%);
        color: red;
        display: flex;
        cursor: pointer;
      }
    }
  }
}

.message {
  padding: 10px !important;
  width: 100%;
  max-width: 400px;
}
