@import 'styles/color.scss';


.root{
  flex-direction: column;
  height: 100%;

  .title{
    font-weight: bold;
    color: $color-11;
  }
  .wrapper{
    flex: 1;
    display: flex;
    flex-wrap: wrap;

    .content{
      height: 280px;
      height: 100%;
      border-radius: 10px !important;
    
      .wrapper{
        padding: 20px;
        height: 100%;
        display: flex;
        flex-direction: column;
    
        .header{
          display: flex;
          flex-direction: row;
            .input{
              padding-left: 10px;
            }
          
          .count{
            h5{
              font-weight: bold
            }
          }
      
          .filter{
            flex: 1;
            display: flex;
            justify-content: flex-end;
          }
        }
      
        .title{
          color: $color-12;
          margin-bottom: 20px;
        }
      
        .list{
          flex: 1;
        }
    
        .action{
          display: flex;
          justify-content: flex-end;
          font-weight: bold;
          color: $color-12;
        }
      }  
    }
  }
 
}
