@import "styles/color.scss";

.root {
  padding-top: 10px;
  padding-bottom: 10px;

  .wrapper {
    padding: 20px;
    padding-bottom: 10px;

    .header {
      .title {
        margin-bottom: 10px;
      }
    }

    .body {
      min-height: 300px;
      max-height: 300px;
      display: flex;
      flex-direction: column;
      align-items: stretch;

      .messages {
        flex: 1;
        display: flex;
        flex-direction: column;

        background-color: whitesmoke;
        padding: 5px;
        overflow: auto;
        padding: 10px;

        .split {
          font-size: 10px;
          text-align: center;
          color: gray;
          margin-top: 5px;
        }

        .balloon {
          display: flex;
          flex-direction: column;
          margin-bottom: 5px;

          .mheader {
            display: flex;
            flex-direction: row;
            align-items: center;

            .sender {
              color: black;
            }

            .time {
              margin-left: 10px;
              font-size: 9pt;
            }
          }

          .mbody {
            max-width: 70%;
            border-radius: 3px;
            .content {
              padding-left: 5px;
              padding-right: 5px;
              padding-top: 5px;
              padding-bottom: 5px;
            }
          }

          &.receive {
            align-items: flex-start;
            .mbody {
              background-color: rgb(166, 245, 150);
            }
          }

          &.me {
            align-items: flex-end;
            .mbody {
              background-color: rgb(167, 227, 251);
            }
          }
        }
      }

      .placeholder {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 13px;
        color: gray;
        font-style: italic;
        text-align: center;
      }
    }

    .action {
      .comment-box {
      }
    }
  }
}
