@import "styles/color.scss";

.root {
  display: flex;
  flex-direction: row;
  padding-top: 10px;
  justify-content: flex-end;

  button {
    margin-left: 10px;
  }
}
.escalate {
  .header-container {
    padding: 0;
    margin: 0;

    .header {
      display: flex;
      align-items: center;
      padding: 10px;

      .title {
        flex: 1;
      }
    }
  }

  .content {
    display: flex;
    flex-direction: column;
    overflow: hidden;
    padding-bottom: 30px;

    .input {
      textarea {
        padding: 20px;
      }
    }

    .footer {
      display: flex;
      flex-direction: row;

      .assign {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 60%;

        .to {
          width: 100px;
        }
      }

      .action {
        width: 40%;
        display: flex;
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;

        > * {
          height: 40px;
          margin-left: 10px;
        }
      }
    }
  }
}
