.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .label {
    padding-bottom: 5px;
  }

  .wrapper {
    display: flex;
    flex-direction: column;

    .read-only-value {
      font-weight: unset;
      padding: 5px;
    }

    .text-field {
      flex: 1;
      display: flex;

      * {
        padding: 5px;
      }

      .submit {
        padding: 0px;
        height: 40px;
        margin: 5px;
      }

      // Errors
      &.has-error {
        border: 1px solid red;
        margin-bottom: unset;
      }
    }

    span.error {
      padding-top: 1px;
      color: red;
    }
  }
}
