@import 'styles/color.scss';

.root {
  display: grid;
  .header {
    .item {
      margin-right: 15px;
      margin-left: 0px;
      color: $color-11;
      cursor: pointer;
      &:hover,
      &.active {
        color: unset;
        text-decoration: underline;
      }
    }
  }
  .divider {
    background: #212a71;
    height: 2px;
  }
  .panels {
    width: 100%;
  }
}
