@import "styles/color.scss";

table {
  font-family: arial, sans-serif;
  border-collapse: collapse;
  width: 100%;
  border: 1px solid #dddddd;

  td,
  th {
    border: 1px solid #dddddd;
    border-left: unset;
    border-right: unset;
    text-align: left;
    padding: 8px;
  }

  tr:nth-child(even) {
    background-color: $color-10;
  }

  .expandable {
    background-color: unset !important;

    td {
      padding: unset;

      .container {
        height: 0;
        overflow: visible;

        transition: all 2s linear 1s;

        .content {
          transition: all 2s linear 1s;
        }

        &.open {
          height: unset;

          .content {
            transform: unset;
          }
        }
      }
    }
  }
}
