@import "styles/color.scss";

.main-contaner,
.wrapper {
  height: 100%;
}

.table {
  width: 100%;
  overflow-x: auto;
}

.table-loading,
.table-no-data {
  align-items: center;
  justify-content: center;
  height: 80px;
}

.text-link {
  color: $color-4;
  cursor: pointer;
  text-decoration: none;
}

.underline {
  text-decoration: underline;
}

.table-title {
  font-size: 1.08rem !important;
}

.table-initial-state {
  padding: 20px;
  text-align: center;
}

.bold {
  font-weight: bold !important;
}

.color-1 {
  color: $color-1;
}
.color-2 {
  color: $color-2;
}
.color-3 {
  color: $color-3;
}
.color-4 {
  color: $color-4;
}
.color-5 {
  color: $color-5;
}
.color-6 {
  color: $color-6;
}
.color-7 {
  color: $color-7;
}
.color-8 {
  color: $color-8;
}
.color-9 {
  color: $color-9;
}
.color-10 {
  color: $color-10;
}
.color-11 {
  color: $color-11;
}
.color-12 {
  color: $color-12;
}

.label,
.label-2 {
  font-weight: bold !important;
  color: $color-11;
}

.content-text,
.content-text-2 {
  font-weight: unset !important;
  color: $color-12;
}

.label-2,
.content-text-2 {
  font-size: 1rem !important;
}

.button-positive {
  background-color: #3AB77D !important;

  &:hover {
    background-color: #0b4930 !important;
  }
}

.button-neutral {
  background-color: #1AB1C4 !important;

  &:hover {
    background-color: #166573 !important;
  }
}

.button-negative {
  background-color: #FD5451 !important;

  &:hover {
    background-color: #D9443c !important;
  }
}
