.root {
  display: flex;
  flex-direction: column;

  .header {
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 20px;

    .graph {
      flex: 1;
      display: flex;
      flex-direction: row;

      .anual-benefit {
        // min-width: 200px;
        position: relative;
        // flex: 1;
      }

      .line-graph {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        flex: 1;

        .container {
          width: 100%;
          padding-left: 20px;

          .line-container {
            display: flex;

            .item {
              min-height: 5px;
            }
          }

          .line-details {
            display: flex;
            margin-top: 10px;

            .item {
              display: flex;
              flex: 1;
              flex-direction: column;

              .label {
                display: flex;

                .color {
                  height: 15px;
                  width: 15px;
                  margin-right: 10px;
                }
              }
            }
          }
        }
      }
    }

    .search {
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }

  .table {
    th {
      padding: 5px;
      font-size: x-small;
      div {
        div {
          margin-bottom: unset !important;
        }
      }

      .search {
        font-size: unset;
        font-weight: normal;
        padding: 5px;
      }
    }

    td {
      vertical-align: unset;
      font-size: x-small;
      padding: 10px;
    }

    .cell {
      display: flex;
      flex-direction: column;

      .label {
        font-weight: bold;
        margin-top: 5px;
      }
    }

    .icon {
      color: #29a11e;
      cursor: pointer;
    }
  }
}
