.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .header {
    display: flex;
    padding-bottom: 5px;

    .delete {
      width: 35px;
      text-align: left;
      margin-right: 10px;
    }

    .description {
      flex: 0.9;
    }

    .code {
      width: 100px;
    }

    .primary {
      text-align: left;
      margin-right: 15px;
    }

    .discrepancy {
      width: 15px;
      text-align: left;
    }
  }

  .wrapper {
    padding-bottom: 20px;

    &:last-child {
      padding-bottom: unset;
    }

    .sub-wrapper {
      display: flex;

      .delete {
        width: 35px;
        text-align: left;
        margin-right: 10px;
      }

      .description {
        flex: 0.9;
      }

      .code {
        width: 100px;
        margin-left: 10px;
      }

      .primary {
        text-align: left;
        margin-right: 15px;
      }
    }
  }
}
