.root {
  display: flex;
  width: 100%;
  flex-direction: column;

  .header {
    display: flex;
    padding-bottom: 5px;

    .description {
      flex: 1;
    }

    .code {
      width: 90px;
      margin: 0 10px;
    }
  }

  .wrapper {
    padding-bottom: 20px;

    &:last-child {
      padding-bottom: unset;
    }

    .sub-wrapper {
      display: flex;

      .description {
        flex: 1;
      }

      .code {
        width: 90px;
        margin: 0 10px;
      }
    }
  }
}
